@tailwind base;
@tailwind components;
@tailwind utilities;
.ed-settings-tabs .tab-active {
  /*border-bottom: 4px solid #007cba;*/
  font-weight: 600; }

.ed-settings-tabs > .components-tab-panel__tabs {
  @apply mb-4; }

.ed-settings-tabs > .components-tab-panel__tab-content {
  @apply shadow;
  @apply bg-white;
  @apply p-4 pt-8 pb-8; }

/* BaseControl Label Fields */
.components-base-control__label {
  @apply text-lg;
  @apply mb-2;
  @apply font-bold; }

.components-base-control__help {
  @apply text-xs;
  @apply text-gray-500;
  @apply mt-1; }

