/* Override Default Content Padding */
#wpcontent {
  padding-left: 0; }

/* Layout Tab */
.ed-layout-settings-tabs .components-tab-panel__tabs {
  @apply flex;
  @apply flex-col; }

.ed-layout-settings-tabs .components-tab-panel__tab-content {
  @apply w-full;
  @apply p-3; }
